
import { IPlayerTank } from "@/types/IPlayer";
import { defineComponent, PropType } from "vue";
import { mapGetters } from "vuex";

export default defineComponent({
    name: "TankListFilter",
    data() {
        return {
            moeList: [
                { name: "3 MoE", value: 3 },
                { name: "2 MoE", value: 2 },
                { name: "1 MoE", value: 1 },
                { name: "0 MoE", value: 0 },
            ],
            tierList: [
                { name: "X", value: 10 },
                { name: "IX", value: 9 },
                { name: "VIII", value: 8 },
                { name: "VII", value: 7 },
                { name: "VI", value: 6 },
                { name: "V", value: 5 },
                { name: "IV", value: 4 },
                { name: "III", value: 3 },
                { name: "II", value: 2 },
                { name: "I", value: 1 },
            ],
            typeList: [
                { name: "Heavy tank", value: "heavyTank" },
                { name: "Medium tank", value: "mediumTank" },
                { name: "Light tank", value: "lightTank" },
                { name: "Tank destroyer", value: "AT-SPG" },
                { name: "SPG", value: "SPG" },
            ],
            moeFilter: [],
            tierFilter: [],
            typeFilter: [],
            nationFilter: [],
            sort: "moe",
        };
    },
    props: {
        tanks: Array as PropType<IPlayerTank[]>,
    },
    watch: {
        moeFilter() {
            this.emitData();
        },
        tierFilter() {
            this.emitData();
        },
        typeFilter() {
            this.emitData();
        },
        nationFilter() {
            this.emitData();
        },
    },
    methods: {
        emitData() {
            const { moeFilter, tierFilter, typeFilter, nationFilter } = this;

            this.$emit("changeFilter", {
                moeFilter,
                tierFilter,
                typeFilter,
                nationFilter,
            });
        },
    },
    computed: {
        ...mapGetters(["tanksList", "nations"]),
        getAmountOfTanks() {
            const { tanks } = this;

            interface Obj {
                [key: string]: number;
            }

            let nations = {} as Obj;
            let tiers = {} as Obj;
            let types = {} as Obj;
            let moe = {} as Obj;

            tanks?.forEach((tank: IPlayerTank) => {
                const { marksOnGun } = tank;
                const { nation, tier, type } = this.tanksList[tank.tank_id];

                if (!nations[nation]) nations[nation] = 0;
                nations[nation]++;

                if (!tiers[tier]) tiers[tier] = 0;
                tiers[tier]++;

                if (!types[type]) types[type] = 0;
                types[type]++;

                if (!moe[marksOnGun]) moe[marksOnGun] = 0;
                moe[marksOnGun]++;
            });

            return { nations, tiers, types, moe };
        },
    },
});
