
import { defineComponent } from "vue";
import { mapActions, mapGetters } from "vuex";
import { IStatistics } from "../types/IStatistics";
import TanksTopList from "../components/TanksTopList.vue";
import Spinner from "../components/Spinner.vue";

export default defineComponent({
    name: "Home",
    components: {
        TanksTopList,
        Spinner,
    },
    data() {
        return {
            isLoading: true,
            error: false,
            statistics: {
                total_players: 0,
                new_players_last_day: 0,
                marked_tank_top_list: [],
                popular_tanks_top_list: [],
                created_at: "",
            } as IStatistics,
        };
    },
    mounted() {
        this.fetchStatistics();
    },
    methods: {
        ...mapActions(["fetchTankList"]),
        fetchStatistics() {
            this.isLoading = true;
            this.error = false;

            fetch("/api/statistics")
                .then((e) => e.json())
                .then((e) => {
                    if (e.success) {
                        this.statistics = e.statistics;
                        return;
                    }

                    this.error = true;
                })
                .catch((err) => {
                    this.error = true;
                })
                .finally(() => {
                    this.isLoading = false;
                });
        },
    },
    computed: {
        ...mapGetters(["isLoadingTankList", "errorTankList"]),
    },
});
