
import { defineComponent } from "vue";
import { mapActions, mapGetters } from "vuex";

import Spinner from "../components/Spinner.vue";
import ClanData from "../components/ClanData.vue";

import { IClan } from "@/types/IClan";

export default defineComponent({
    name: "ClanPage",
    components: {
        ClanData,
        Spinner,
    },
    data() {
        return {
            error: false,
            clanNotFound: false,
            isLoading: true,
            clan: {} as IClan,
        };
    },
    mounted() {
        this.fetchClan();
    },
    watch: {
        "$route.params"() {
            this.fetchClan();
        },
    },
    methods: {
        ...mapActions(["fetchTankList"]),
        fetchClan() {
            this.isLoading = true;
            this.error = false;
            this.clanNotFound = false;

            const name = this.$route.params.clan;

            fetch(`/api/clan/${name}`)
                .then((e) => e.json())
                .then((e) => {
                    if (e.success) {
                        this.clan = e.data;
                        return;
                    }
                    if (e.clanNotFound) {
                        this.clanNotFound = true;
                        return;
                    }

                    this.error = true;
                })
                .catch(() => {
                    this.error = true;
                })
                .finally(() => {
                    this.isLoading = false;
                });
        },
    },
    computed: {
        ...mapGetters(["tanksList", "isLoadingTankList", "errorTankList"]),
    },
});
